* {
  font-family: -apple-system, Helvetica, Arial, sans-serif;
}

body {
  padding: 1rem;
}

input {
  width: 100%;
  font-size: 1.25rem;
  color: gray;
}

.header {
  display: flex;
  align-content: center;
  justify-content: space-between;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
